





































import TextArea from "@/components/text-area.vue";
import { eventHub } from "@/components/eventHub";

export default {
  components: { TextArea },
  props: {
    task: {},
    solution: {},
    edition: {
      type: Boolean
    },
    id: {}
  },
  methods: {
    runScript() {
      eventHub.$emit("execute-code-" + this.id);
    },
    back() {
      this.$emit("back");
    },
    next() {
      this.$emit("next");
    }
  }
};
